:root {
  --primary-color: #5b2088;
  --secondary-color: #25d4ac;
  --card-back-text: #BFB7B6;
  --anchor-color: #2b74d4;
}

* {
  font-family: "Lato", Arial, Helvetica, sans-serif;
}

.portfolio {
  background-image: url(/Portfolio-React/static/media/Pngtree.38068263.jpg);
/*  background-image: url(../images/home-page.jpg); /**/
}

.container {
  margin-top: 3rem;

}

main {
  min-height: calc(100vh - 270px);
}

.about, .work-title, .contact-title, #resume{
  text-align: center;
  padding-top: 2rem;
  color: #5b2088;
  color: var(--primary-color);
}

/* About */
#pfp {
  width: 250px;
  border-radius: 100%;
}

p {
  font-size: 1.3rem;
/*  font-weight: bold; /**/
-webkit-backdrop-filter: blur(10px);
backdrop-filter: blur(10px);
}

/* Navbar */
#nav {
  height: 75px;
/**/  background-color: #5b2088;  background-color: var(--primary-color); /**/
}

.justify-content-center {
    justify-content: center!important;
}

.nav {

    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }

.nav-item li {
  color: #25d4ac;
  color: var(--secondary-color);
}

.nav-item span {
  color: #25d4ac;
  color: var(--secondary-color);
  font-size: 1.5rem;
  padding-top: 2rem;

}

.nav-item span:hover {
  color: #2b74d4;
  color: var(--anchor-color);
  cursor: pointer;
}

.nav-link {
    display: block;
    padding: 1rem 1rem;
}

.nav-link span{
  color: #25d4ac;
  color: var(--secondary-color);

}

.navActive span{
  color: white;
  text-decoration: none;
}

/* Projects */

.project-items {
  width: 23.438rem;
  height: 12.5rem;
  border: 2px solid black;
  margin: 1rem;
  padding: 0 !important;
  position: relative;
}

.projects {
  padding-top: 2rem;
}

.project-items img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-text {
  position:absolute;
  top: 8px;
  left: 16px;
  visibility: hidden;
  opacity: 0;
  transition: opacity .4s, visibility .6s;
  color: #25d4ac;
  color: var(--secondary-color);
}

.image-text h4 {
  font-weight: bolder;
}

.image-text p {
  color: #BFB7B6;
  color: var(--card-back-text);
}

.image-text a {
  text-decoration: none;
  color: #25d4ac;
  color: var(--secondary-color);
  padding-right: 1rem;
}

.image-text a:hover {
  color: #abd8cd;
}

.project-items:hover .image-text {
  visibility: visible;
  opacity: 1;
}

.project-items:hover img{
  -webkit-filter: blur(3px) brightness(30%);
          filter: blur(3px) brightness(30%);
  
}

/* Contact */
#contact-form {
  padding-left: 10%;
  padding-right: 10%;
}

#contact-form .btn{
  color: #25d4ac;
  color: var(--secondary-color);
  background-color: #5b2088;
  background-color: var(--primary-color);
}

/* Resume*/

.container #resume {
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

#resume .btn{
  background-color: #5b2088;
  background-color: var(--primary-color);
  transition: .3s; 
}

#resume a {
  text-decoration: none;
  color: #25d4ac;
  color: var(--secondary-color);
}

#resume .btn:hover {
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
  transition: .3s;
}
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  70% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  70% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


.skills-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: row;
  grid-column-gap: 7rem;
  -webkit-column-gap: 7rem;
          column-gap: 7rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.skills-wrapper ul {
  padding: 0;
}

.skills-wrapper li {
  list-style-type: none;
  font-size: 1.3rem;
}

/* Footer */
footer h3 {
  font-weight: bold;
  font-size: 1.8rem;
}

footer {
  height: 100px;
  border-top: 2px solid #5b2088;
  border-top: 2px solid var(--primary-color);

}

.socials {
/**/  color: #5b2088;  color: var(--primary-color); /**/
  width: 100%;
  text-align: center;
  margin-top: auto;

}

footer i {
  width: 100%;
  height: 16px;
  padding: .8rem;
/**/  color: #5b2088;  color: var(--primary-color); /**/

